.infoBox {
    flex: 1;
  }
  
 

  .infoBox:not(:last-child) {
    margin-right: 10px;
  }
  
  .infoBox--selected {
    border-top: 10px solid greenyellow;
  }
  
  .infoBox--red {
    border-color: red;
  }

  
  .infoBox__cases--green {
    color: lightgreen !important;
  }
  
  .infoBox__cases {
    color: #fc3c3c;
    font-weight: 600;
    font-size: 3rem;
    margin-bottom: 0.5rem;
  }
  
  .infoBox__total {
    color: #17191b;
    font-weight: 700 !important;
    font-size: 1rem !important;
    margin-top: 15px !important;
  }
  